import { EmbeddingVizStateWithDataModel, EmbeddingAbstractQuery } from '@tableau/api-external-contract-js';
import { ApiService } from '@tableau/api-shared-js';

/**
 * Service for using VizAPI
 *
 * @interface EmbeddingVizApiService
 * @extends {ApiService}
 */
export interface EmbeddingVizApiService extends ApiService {
  /**
   * Returns the embedding viz state with data model
   *
   * @returns {Promise<EmbeddingVizStateWithDataModel>} - Embedding Viz State With Data Model.
   * @ignore
   */
  getEmbeddingVizStateWithDataModelAsync(): Promise<EmbeddingVizStateWithDataModel>;

  /**
   * Sets the embedding viz state with data model
   *
   * @param {EmbeddingVizStateWithDataModel} embeddingVizStateWithDataModel - The viz state with data model.
   * @returns {Promise<void>} Promise which resolves to void.
   * @ignore
   */
  setEmbeddingVizStateWithDataModelAsync(embeddingVizStateWithDataModel: EmbeddingVizStateWithDataModel): Promise<void>;

  /**
   * Returns the embedding abstract query
   *
   * @returns {Promise<Embedding abstract query>} - The base64 encoded abstract query.
   * @ignore
   */
  getEmbeddingAbstractQueryAsync(): Promise<EmbeddingAbstractQuery>;
}
